<template>
    <div v-show="aiShow">
        <div class="ai-setting-shade"  :style="{'align-items': clientHeight < 800 ? 'flex-start' : 'center'}">
            <div v-show="aiShow" class="ai-active" :style="{width: showMethod ? '1071px' : '720px'}">
                <div class="ai-header">
                    <span>行为管理</span>
                    <span class="modal-close" @click="handleCloseModal(2)">X</span>
                </div>
                <div class="ai-setting-list">
                    <div class="active-list">
                        <div class="active-1">
                            <div class="active-header">
                                <span class="active-title">行为设置</span>
                            </div>
                            <div class="active-item">
                                <span>名称：</span>
                                <i-input v-model="active.BehaviorName" class="column-input" style="width: 228px;"></i-input>
                            </div>
                            <div class="active-item">
                                <Checkbox v-model="active.Attack.Check">开战</Checkbox>
                                <InputNumber :min="0" v-model="active.Attack.StartTime" class="columns-item-input"></InputNumber>
                                <span>至</span>
                                <InputNumber :min="0" v-model="active.Attack.EndTime" class="columns-item-input"></InputNumber>
                                <span>秒内使用方案进行攻击。（需勾选方案“启用”按钮）</span>
                            </div>
                            <div class="active-item">
                                <Checkbox v-model="active.AttackQuery.Check">开战</Checkbox>
                                <InputNumber :min="0" v-model="active.AttackQuery.StartTime" class="columns-item-input" style="width: 50px;"></InputNumber>
                                <span>至</span>
                                <InputNumber :min="0" v-model="active.AttackQuery.EndTime" class="columns-item-input" style="width: 50px;"></InputNumber>
                                <span>秒内使用查询指令，几率为</span>
                                <InputNumber :min="0" v-model="active.AttackQuery.Probability" class="columns-item-input" style="width: 50px;"></InputNumber>
                                <span>(1为100%)</span>
                                <span>命令</span>
                                <i-input v-model="active.AttackQuery.Keyword" class="column-input" style="width: 130px;"></i-input>
                            </div>
                            <div class="active-item">
                                <Checkbox v-model="active.ArmisticeQuery.Check">休战</Checkbox>
                                <InputNumber :min="0" v-model="active.ArmisticeQuery.StartTime" class="columns-item-input" style="width: 50px;"></InputNumber>
                                <span>至</span>
                                <InputNumber :min="0" v-model="active.ArmisticeQuery.EndTime" class="columns-item-input" style="width: 50px;"></InputNumber>
                                <span>秒内使用查询指令，几率为</span>
                                <InputNumber :min="0" v-model="active.ArmisticeQuery.Probability" class="columns-item-input" style="width: 50px;"></InputNumber>
                                <span>(1为100%)</span>
                                <span>命令</span>
                                <i-input v-model="active.ArmisticeQuery.Keyword" class="column-input" style="width: 130px;"></i-input>
                            </div>
                            <div class="active-item">
                                <Checkbox v-model="active.UpCmd.Check">余额低于</Checkbox>
                                <InputNumber :min="0" v-model="active.UpCmd.Limit" class="columns-item-input" style="width: 107px;"></InputNumber>
                                <span>时，发送上分指令，操作金额</span>
                                <InputNumber :min="0" v-model="active.UpCmd.Variety" class="columns-item-input" style="width: 107px;"></InputNumber>
                                <span>命令</span>
                                <i-input v-model="active.UpCmd.Keyword" class="column-input" style="width: 130px;"></i-input>
                            </div>
                            <div class="active-item">
                                <Checkbox v-model="active.DownCmd.Check">余额高于</Checkbox>
                                <InputNumber :min="0" v-model="active.DownCmd.Limit" class="columns-item-input" style="width: 107px;"></InputNumber>
                                <span>时，发送下分指令，操作金额</span>
                                <InputNumber :min="0" v-model="active.DownCmd.Variety" class="columns-item-input" style="width: 107px;"></InputNumber>
                                <span>命令</span>
                                <i-input v-model="active.DownCmd.Keyword" class="column-input" style="width: 130px;"></i-input>
                            </div>
                            <div class="active-item">
                                <Checkbox v-model="active.StopCmd.Check">余额低于</Checkbox>
                                <InputNumber :min="0" v-model="active.StopCmd.Limit" class="columns-item-input" style="width: 107px;"></InputNumber>
                                <span>时，停止攻击</span>
                                <Checkbox style="marginLeft: 50px;" v-model="active.EndPoint">游戏结束时下分</Checkbox>
                            </div>
                        </div>
                        <div class="active-2">
                            <p class="poptip-header">行为设置说明</p>
                            <ul>
                                <li>1.攻击时间可根据需求酌情微调，攻击时间范围尽量覆盖对应游戏类型的整个未封盘时间区间。</li>
                                <li>2.开战休战订单查询和流水查询，默认触发概率0.1比较接近实际场景，各个行为中的开战和休战起止尽量设置一些差异。</li>
                                <li>3.余额低于XX停止攻击，默认不勾选。选中后会根据场景触发停止运行。</li>
                                <li>4.余额低于XX上分，此数值的比例请对应您的方案金额。不然会出现冲大买小的现象。</li>
                            </ul>
                        </div>
                        <div class="active-2">
                            <p class="poptip-header">方案管理说明</p>
                            <ul>
                                <li>1.方案列表中，每多一个方案，则会在每期多一个投注订单，订单是从方案中随机抽取的。重盖概率接近于0。用户可根据自己的情况在方案中增加、修改和删除。</li>
                                <li>2.默认方案已经设置了近万种组合，如果不愿意自定义，则直接使用预设的方案就能满足几乎全部场景。</li>
                            </ul>
                        </div>
                        <div class="method-operate">
                            <span @click="handleSaveBehavior">保存</span>
                            <span @click="handleCloseModal(2)">取消</span>
                        </div>
                    </div>
                    <div v-show="showMethod" class="method-list">
                        <div class="active-header">
                            <span class="active-title">方案设置</span>
                        </div>
                        <div class="method-l">
                            <div class="method-item">
                                <span>方案列表：</span>
                                <Select v-model="method.checkId" style="width:110px" @on-change="handleChangeMethod">
                                    <Option v-for="(item, index) in methodHeaderList" :value="item.id" :key="index">{{ item.name }}</Option>
                                </Select>
                                <Select v-model="method.checkType" style="width:110px" disabled>
                                    <Option value="1">不翻倍</Option>
                                    <Option value="2">中翻倍</Option>
                                    <Option value="3">不中翻倍</Option>
                                </Select>
                            </div>
                            <div class="method-item">
                                <span>金额设置：</span>
                                <i-input v-model="method.money" class="column-input" style="width: 176px;" disabled></i-input>
                                <Checkbox style="marginLeft: 2px;" disabled v-model="method.enable">启用</Checkbox>
                            </div>
                        </div>
                        <div class="bet-list">
                            <p>此方案可随机抽取注单列表（点击表格添加或修改）</p>
                            <p v-for="(item, index) in method.betList" :key="index">{{item.value}}</p>
                        </div>
                        <div class="method-item operate-list">
                            <span @click="addShow = true">添加方案</span>
                            <span @click="selectMethod = method;modifyShow = true">修改方案</span>
                            <span @click="handleDeleteMethod">删除方案</span>
                        </div>
                    </div>
                </div>
            </div>
            <div v-show="addShow || modifyShow" class="edit-panel">
                <div class="ai-header">
                    <span>{{addShow ? '新增方案' : '修改方案'}}</span>
                    <span class="modal-close" @click="handleCloseModal(3)">X</span>
                </div>
                <div class="edit-item">
                    <span>方案名称：</span>
                    <i-input v-model="selectMethod.name" style="width:140px"></i-input>
                    <Select v-model="selectMethod.checkType" style="width:140px">
                        <Option value="1">不翻倍</Option>
                        <Option value="2">中翻倍</Option>
                        <Option value="3">不中翻倍</Option>
                    </Select>
                </div>
                <div class="edit-item">
                    <span>金额设置：</span>
                    <i-input v-model="selectMethod.money" style="width: 228px;"></i-input>
                    <Checkbox style="marginLeft: 2px;" v-model="selectMethod.enable">启用</Checkbox>
                </div>
                <div class="edit-item">
                    <span>投注信息：</span>
                    <div class="edit-operate">
                        <span @click="handleAddBet" class="plus"></span>
                        <span @click="handleMinusBet" class="minus"></span>
                    </div>
                </div>
                <div class="edit-item">
                    <ul>
                        <li v-for="(item, index) in selectMethod.betList" :key="index">
                            <i-input v-model="item.value" class="column-input" placeholder="请填写投注信息"></i-input>
                        </li>
                    </ul>
                </div>
                <div class="edit-item save-item">
                    <span v-text="addShow ? '确定保存' : '修改'" @click="handleOperate"></span>
                    <span @click="handleAddList">导入模板</span>
                </div>
            </div>
            <div v-if="batchShow" class="add-batch">
                <div class="batch-header">
                    <p class="batch-title">批量添加<span class="modal-close" @click="handleCloseModal(4)">X</span></p>
                    <p class="batch-poptip">各个投注信息之间用逗号","或者分号";"隔开，支持中文符号</p>
                </div>
                <div class="batch-content">
                    <textarea v-model="batchContent"></textarea>
                </div>
                <div class="enter-add" @click="handleEnterBatch">确认添加</div>
                <div class="list-operate">
                    <div class="import-item" @click="importTenMethod">导入10球模板</div>
                    <div class="import-item" @click="importFiveMethod">导入5球模板</div>
                </div>
            </div>
            <loading :loading="isLoading"></loading>
            <Modal v-model="showDelete" title="确认删除" @on-ok="handleEnterDelete" @on-cancel="showDelete = false">
                <p>是否确认删除？</p>
            </Modal>
        </div>
    </div>
</template>

<script>
export default {
    props: {
        aiShow: {
            type: Boolean,
            default: false
        },
        id: {
            type: String
        }
    },
    data() {
        let that = this;
        return {
            showMethod: false,
            batchContent: '',
            clientHeight: document.body.clientHeight,
            showDelete: false,
            addShow: false,
            modifyShow: false,
            activeShow: false,
            showAcitveCol: [
                {
                    title: '名称',
                    key: 'name',
                    align: 'center'
                },
                {
                    title: '方案数量',
                    key: 'num',
                    align: 'center'
                },
                {
                    desc: 'operate',
                    title: '操作',
                    className: 'record-item',
                    align: 'center',
                    render: (h, params) => {
                        return h('div', [
                            h('Button', {
                                props: {
                                    type: 'primary',
                                    size: 'small'
                                },
                                style: {
                                    marginRight: '5px'
                                },
                                on: {
                                    click: () => {
                                        that.handleShowActive(params.row);
                                    }
                                }
                            }, '修改行为'),
                        ])
                    }
                }
            ],
            aiSettingList: [
                {
                    id: '',
                    name: 'test',
                    num: 2
                }
            ],
            active: {
                BehaviorId: '',
                BehaviorName: '演示模板-10球',
                Attack: {
                    StartTime: 15,
                    EndTime: 250,
                    Check: true
                },
                AttackQuery: {
                    keyword: '查',
                    Probability: 0.1,
                    StartTime: 20,
                    EndTime: 120,
                    Check: true
                },
                ArmisticeQuery: {
                    Keyword: '查',
                    Probability: 0.1,
                    StartTime: 15,
                    EndTime: 60,
                    Check: true
                },
                UpCmd: {
                    Limit: 500,
                    Variety: 1000,
                    Keyword: '上分',
                    Check: true
                },
                DownCmd: {
                    Limit: 2000,
                    Variety: 1000,
                    Keyword: '下分',
                    Check: true
                },
                StopCmd: {
                    Limit: 0,
                    Check: true
                },
                EndPoint: false
            },
            method: {
                checkId: '',
                checkType: '',
                name: '',
                money: '',
                betList: [],
                enable: false
            },
            selectMethod: {
                checkId: '',
                checkType: '1',
                name: '',
                money: '',
                betList: [
                    {
                        value: ''
                    }
                ],
                enable: false
            },
            isLoading: false,
            isMLoading: false,
            methodHeaderList: [],
            batchShow: false
        }
    },
    watch: {
        aiShow(bool) {
            let that = this;
            if (bool) {
                that.active.BehaviorId = that.id;
                that.methodHeaderList = [];
                if (that.id) {
                    that.showMethod = true;
                    that.handleGetBehaviorInfo();
                    that.handleGetMethodList();
                } else {
                    that.method = {
                        checkId: '',
                        checkType: '',
                        name: '',
                        money: '',
                        betList: [],
                        enable: false
                    };
                    that.active = {
                        BehaviorId: '',
                        BehaviorName: '演示模板-10球',
                        Attack: {
                            StartTime: 15,
                            EndTime: 250,
                            Check: true
                        },
                        AttackQuery: {
                            Keyword: '查',
                            Probability: 0.1,
                            StartTime: 20,
                            EndTime: 120,
                            Check: true
                        },
                        ArmisticeQuery: {
                            Keyword: '查',
                            Probability: 0.1,
                            StartTime: 15,
                            EndTime: 60,
                            Check: true
                        },
                        UpCmd: {
                            Limit: 500,
                            Variety: 1000,
                            Keyword: '上分',
                            Check: true
                        },
                        DownCmd: {
                            Limit: 2000,
                            Variety: 1000,
                            Keyword: '下分',
                            Check: true
                        },
                        StopCmd: {
                            Limit: 0,
                            Check: true
                        },
                        EndPoint: false
                    };
                }
            }
        }
    },
    methods: {
        importTenMethod() {
            let that = this;
            that.handleSendFile('ten');
        },
        importFiveMethod() {
            let that = this;
            that.handleSendFile('five');
        },
        handleSendFile(type) {
            let that = this,
                href = '';

            switch(type) {
                case 'five':
                    href = '/pro/5ball.txt';
                break;
                case 'ten':
                    href = '/pro/10ball.txt';
                break;
            }

            if (!that.isMLoading) {
                that.isMLoading = true;

                that.$axios({
                    url: href
                }).then(function(data) {
                    that.isMLoading = false;
                    if (data.status === 200) {
                        const result = data.data.split(/\n/);
                        that.selectMethod.betList = result.map(item => {return {value: item}});
                        that.batchShow = false;
                    }
                })
            }
        },
        handleDeleteMethod() {
            let that = this;

            if (that.method.checkId) {
                that.showDelete = true;
            } else {
                that.$Message.error('没有选择方案，不能删除！');
            }
        },
        handleEnterBatch() {
            let that = this,
                batchContent = that.batchContent.replace(/,|，|；|;|\n|\r|\r\n|↵/g, ';');

            let batchList = batchContent.split(';');
            that.selectMethod.betList = batchList.map(item => {return {value: item}});
            that.batchShow = false;
        },
        handleAddList() {
            let that = this;
            that.batchShow = true;
        },
        handleChangeMethod() {
            let that = this;

            if (!that.isMLoading) {
                that.isMLoading = true;

                that.$axios({
                    url: '/api/Setup/GetProgramInfo',
                    params: {
                        programID: that.method.checkId
                    }
                }).then(function(data) {
                    that.isMLoading = false;

                    if (data.data.Status === 100) {
                        const result = data.data.Model;

                        that.method = {
                            checkId: result.ProgramID,
                            name: result.ProgramName,
                            checkType: result.DoubleType + '',
                            money: result.Amountset,
                            enable: result.IsEnable,
                            betList: result.BetTypeList.map(item => {return {value: item}})
                        }
                    }
                })
            }
        },
        handleOperate() {
            let that = this;

            if (that.addShow) {
                that.handleModifyMethod('/api/Setup/AddProgramInfo');
            } else {
                that.handleModifyMethod('/api/Setup/PutProgramInfo');
            }
        },
        handleEnterDelete() {
            let that = this;

            if (!that.isMLoading) {
                that.isMLoading = true;

                that.$axios({
                    url: '/api/Setup/DeleteProgramInfo',
                    method: 'post',
                    params: {
                        programID: that.method.checkId
                    }
                }).then(function(data) {
                    that.isMLoading = false;

                    if (data.data.Status === 100) {
                        that.$Message.success(data.data.Message);
                        that.method = {
                            checkId: '',
                            checkType: '',
                            name: '',
                            money: '',
                            betList: [],
                            enable: false
                        };
                        that.handleGetMethodList();
                    } else {
                        that.$Message.error(data.data.Message);
                    }
                })
            }
        },
        handleModifyMethod(href) {
            let that = this,
                amount = that.selectMethod.money.replace(/，/g, ',').split(','),
                flag = false;

            amount.forEach(function(item) {
                if (!Number(item)) {
                    flag = true;
                }
            })

            if (flag) {
                that.$Message.error('金额设置有误，请重新设置');
                return;
            }

            if (!that.selectMethod.money || !that.selectMethod.name) {
                that.$Message.error('方案信息有误，请重新设置');
                return;
            }

            if (that.selectMethod.betList.filter(item => item.value).length === 0) {
                that.$Message.error('投注信息有误，请重新设置');
                return;
            }

            if (!that.isMLoading) {
                that.isMLoading = false;

                that.$axios({
                    url: href,
                    method: 'post',
                    data: {
                        "BehaviorID": that.id,
                        "ProgramID": that.selectMethod.checkId,
                        "ProgramName": that.selectMethod.name,
                        "DoubleType": that.selectMethod.checkType,
                        "Amountset": that.selectMethod.money.replace('，', ','),
                        "IsEnable": that.selectMethod.enable,
                        "BetTypeList": that.selectMethod.betList.map(item => {return item.value})
                    }
                }).then(function(data) {
                    that.isMLoading = false;

                    if (data.data.Status === 100) {
                        that.$Message.success(data.data.Message);
                        if (that.addShow) {
                            that.handleGetMethodList();
                        }
                        that.addShow = false;
                        if (that.modifyShow) {
                            that.handleGetMethodList();
                        }
                        that.modifyShow = false;
                        that.selectMethod.name = '';
                        that.selectMethod.checkType = '';
                        that.selectMethod.money = '';
                        that.selectMethod.enable = false;
                        that.selectMethod.betList = [];
                    } else {
                        that.$Message.error(data.data.Message);
                    }
                })
            }
        },
        handleGetMethodList() {
            let that = this;

            if (!that.isMLoading) {
                that.isMLoading = true;

                that.$axios({
                    url: '/api/Setup/GetProgramList',
                    params: {
                        behaviorID: that.id
                    }
                }).then(function(data) {
                    that.isMLoading = false;

                    if (data.data.Status === 100) {
                        that.methodHeaderList = data.data.Data.map(item => {
                            return {
                                name: item.ProgramName,
                                id: item.ProgramID
                            }
                        })
                        if (that.methodHeaderList.length) {
                            if (!that.method.checkId) {
                                that.method.checkId = that.methodHeaderList[0].id;
                            }
                            that.handleChangeMethod();
                        }
                    }
                })
            }
        },
        handleGetBehaviorInfo() {
            let that = this;

            if (!that.isLoading) {
                that.isLoading = true;

                that.$axios({
                    url: '/api/Setup/GetBehaviorInfo',
                    params: {
                        behaviorID: that.active.BehaviorId
                    }
                }).then(function(data) {
                    that.isLoading = false;

                    if (data.data.Status === 100) {
                        that.active = data.data.Model;
                    }
                })
            }
        },
        handleAddBet() {
            let that = this,
                len = that.selectMethod.betList.length;

            that.selectMethod.betList.splice(len, 0, {value: ''});
        },
        handleMinusBet() {
            let that = this,
                len = that.selectMethod.betList.length;

            if (len === 1) {
                that.$Message.error('至少保留一个投注信息');
            } else {
                that.selectMethod.betList.splice(len - 1, 1);
            }
        },
        handleCloseModal(type) {
            let that = this;

            if (type === 1 || type === 2) {
                that.showMethod = false;
                that.method = {
                    checkId: '',
                    checkType: '',
                    name: '',
                    money: '',
                    betList: [],
                    enable: false
                };
                that.active = {
                    BehaviorId: '',
                    BehaviorName: '',
                    Attack: {
                        StartTime: 0,
                        EndTime: 0,
                        Check: false
                    },
                    AttackQuery: {
                        keyword: '',
                        Probability: 0,
                        StartTime: 0,
                        EndTime: 0,
                        Check: false
                    },
                    ArmisticeQuery: {
                        Keyword: '',
                        Probability: 0,
                        StartTime: 0,
                        EndTime: 0,
                        Check: false
                    },
                    UpCmd: {
                        Limit: 0,
                        Variety: 0,
                        Keyword: '',
                        Check: false
                    },
                    DownCmd: {
                        Limit: 0,
                        Variety: 0,
                        Keyword: '',
                        Check: false
                    },
                    StopCmd: {
                        Limit: 0,
                        Check: false
                    },
                    EndPoint: false
                };
            }
            switch(type) {
                case 1:
                    that.$emit('closeModal');
                break;
                case 2:
                    that.$emit('closeModal');
                break;
                case 3:
                    that.addShow = false;
                    that.modifyShow = false;
                    that.batchShow = false;
                break;
                case 4:
                    that.batchShow = false;
                break;
            }
        },
        handleShowActive(item) {
            let that = this;

            that.activeShow = true;
        },
        handleSaveBehavior() {
            let that = this,
                href = '';

            if (!that.active.BehaviorName) {
                that.$Message.error('请输入行为名称');
                return;
            }

            if (that.id) {
                href = '/api/Setup/PutBehaviorInfo';
            } else {
                href = '/api/Setup/AddBehaviorInfo';
            }

            if (!that.isLoading) {
                that.isLoading = true;

                that.$axios({
                    url: href,
                    method: 'post',
                    data: {
                        "BehaviorID": that.id,
                        "BehaviorName": that.active.BehaviorName,
                        "Attack": {
                            "StartTime": that.active.Attack.StartTime,
                            "EndTime": that.active.Attack.EndTime,
                            "Check": that.active.Attack.Check
                        },
                        "AttackQuery": {
                            "Probability": that.active.AttackQuery.Probability,
                            "Keyword": that.active.AttackQuery.Keyword,
                            "StartTime": that.active.AttackQuery.StartTime,
                            "EndTime": that.active.AttackQuery.EndTime,
                            "Check": that.active.AttackQuery.Check
                        },
                        "ArmisticeQuery": {
                            "Probability": that.active.ArmisticeQuery.Probability,
                            "Keyword": that.active.ArmisticeQuery.Keyword,
                            "StartTime": that.active.ArmisticeQuery.StartTime,
                            "EndTime": that.active.ArmisticeQuery.EndTime,
                            "Check": that.active.ArmisticeQuery.Check
                        },
                        "UpCmd": {
                            "Check": that.active.UpCmd.Check,
                            "Limit": that.active.UpCmd.Limit,
                            "Variety": that.active.UpCmd.Variety,
                            "Keyword": that.active.UpCmd.Keyword
                        },
                        "DownCmd": {
                            "Check": that.active.DownCmd.Check,
                            "Limit": that.active.DownCmd.Limit,
                            "Variety": that.active.DownCmd.Variety,
                            "Keyword": that.active.DownCmd.Keyword
                        },
                        "StopCmd": {
                            "Check": that.active.StopCmd.Check,
                            "Limit": that.active.StopCmd.Limit
                        },
                        "EndPoint": that.active.EndPoint
                    }
                }).then(function(data) {
                    that.isLoading = false;

                    if (data.data.Status === 100) {
                        that.$Message.success(data.data.Message);
                        if (!that.id) {
                            that.showMethod = true;
                            that.$emit('editBehavior', data.data.Keyword);
                        }
                    }
                })
            }
        }
    }
}
</script>

