<template>
    <div class="false-user">
        <div class="false-user-list">
            <div class="false-user-header">
                <span class="false-user-title">虚拟用户列表</span>
                <p class="false-user-poptip">
                    <span>注：该列表只显示通过右侧增加的虚拟用户，正常改虚拟的不在此显示</span>
                    <br/>
                    <span>新增虚拟用户后，需要设置游戏行为后才能启用</span>
                </p>
            </div>
            <div class="partition-line"></div>
            <div class="list-data">
                <div class="data-header">
                    <p class="item-info">头像</p>
                    <p class="item-info">用户名</p>
                    <p class="item-info">ID</p>
                    <p class="item-info">账户余额</p>
                    <p class="item-info">选择游戏</p>
                    <p class="item-info">是否启用</p>
                    <p class="item-info">操作</p>
                </div>
                <div class="data-con">
                    <div class="data-item" v-for="(item, index) in falseUserData" :key="index">
                        <div class="item-info">
                            <img :src="item.header" @error="handleImageError(item)" alt title/>
                        </div>
                        <div class="item-info" :title="item.nickname">{{item.nickname}}</div>
                        <div class="item-info">{{item.showId}}</div>
                        <div class="item-info">{{item.balance}}</div>
                        <div class="item-info">{{item.openGame}}</div>
                        <div class="item-info">
                            <switchs :open="item.open" @switch="handleSwitch(item)"></switchs>
                        </div>
                        <div class="item-info operate">
                            <div class="item-button" @click="handleShowModifyUser(item)">设置</div>
                            <div class="item-button" @click="handleDeleteUser(item)">删除</div>
                        </div>
                    </div>
                </div>
                <Page class="footer-page" :total="page.total" :page-size="page.pageSize" :current="page.pageNum" show-sizer @on-page-size-change="handleChangeSize" @on-change="handleChangePage"/>
            </div>
        </div>
        <div class="false-user-operate">
            <div class="operate-list">
                <div class="false-user-header">
                    <span class="false-user-title">操作设置</span>
                </div>
                <div>
                    <div class="operate-item" @click="handleShowAddUser">增加虚拟用户</div>
                    <div class="operate-item" @click="handleOperateUser(true)">启用全部用户</div>
                    <div class="operate-item" @click="handleOperateUser(false)">停用全部用户</div>
                </div>
            </div>
            <div class="behavior">
                <div class="false-user-header">
                    <span class="false-user-title">操作设置</span>
                    <span class="false-user-ope" @click="handleShowAI">添加行为</span>
                </div>
                <div class="behavior-list">
                    <div class="behavior-header">
                        <span>名称</span>
                        <span>方案数量</span>
                        <span>操作</span>
                    </div>
                    <div class="behavior-c">
                        <div class="behavior-c-item" v-for="(item, index) in behaviorList" :key="index">
                            <span>{{item.name}}</span>
                            <span>{{item.num}}</span>
                            <div class="item-o">
                                <span @click="handleShowEdit(item.id)">修改</span>
                                <span @click="handleShowDelete(item.id)">删除</span>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
        <div v-show="addFalseUser.show || modifyFalseUser.show" class="false-user-mask">
            <div v-show="addFalseUser.show" class="add-false-user">
                <div class="add-header">
                    <span>添加虚拟用户</span>
                    <span class="modal-close" @click="handleCloseModal('addFalseUser')">X</span>
                </div>
                <div class="add-content">
                    <div class="add-item">
                        <p class="columns-item-label">用户昵称</p>
                        <i-input v-model="addFalseUser.nickname" class="columns-item-input"></i-input>
                    </div>
                    <div v-if="false" class="add-item">
                        <p class="columns-item-label">用户头像</p>
                        <div class="columns-item-input upload-img" @click="handleUploadImg">
                            <img v-if="addFalseUser.header" :src="addFalseUser.header" alt="收款图片"/>
                            <span>点击上传图片</span>
                        </div>
                    </div>
                    <div v-if="false" class="add-item">
                        <p class="columns-item-label">假人状态</p>
                        <RadioGroup v-model="addFalseUser.status" class="columns-item-input">
                            <Radio v-for="(item, index) in falseUserStatusList" :key="index" :label="item.value">
                                <span>{{item.title}}</span>
                            </Radio>
                        </RadioGroup>
                    </div>
                    <div class="add-item">
                        <i-button @click="handleAddFalseUser">添加</i-button>
                    </div>
                </div>
            </div>
            <div v-show="modifyFalseUser.show" class="modify-false-user">
                <div class="modify-header">
                    <span>虚拟用户设置</span>
                    <span class="modal-close" @click="handleCloseModal('modifyFalseUser')">X</span>
                </div>
                <div class="modify-content">
                    <div class="modify-c-h">
                        <div class="modify-item">
                            <img class="user-header" v-if="modifyFalseUser.header" :src="showUploadImg || addFalseUser.header || modifyFalseUser.header" alt="用户头像"/>
                            <p class="change-header" @click="showUploadHeader = !showUploadHeader">更改头像</p>
                        </div>
                        <div class="modify-list">
                            <div class="modify-column">
                                <div class="modify-item">
                                    <span class="columns-item-label">昵称</span>
                                    <i-input v-model="modifyFalseUser.nickname" class="columns-item-input"></i-input>
                                </div>
                                <div class="modify-item">
                                    <span class="columns-item-label">ID</span>
                                    <i-input disabled v-model="modifyFalseUser.showId" class="columns-item-input"></i-input>
                                </div>
                            </div>
                            <div class="modify-column">
                                <div class="modify-item column">
                                    <span>选择手动发言房间：</span>
                                    <select v-model="modifyFalseUser.gameType">
                                        <option v-for="(ele, ind) in gameList" :key="ind" :value="ele.type">{{ele.name}}</option>
                                    </select>
                                </div>
                                <div class="modify-item input">
                                    <input v-model="modifyFalseUser.inputText" placeholder="请输入聊天内容"/>
                                </div>
                                <div class="modify-item">
                                    <div class="send-button" @click="handleSend">发送聊天</div>
                                </div>
                            </div>
                        </div>
                    </div>
                    <div class="modify-c-b">
                        <div class="false-user-header">
                            <span class="false-user-title">账户参与游戏及下注行为选择</span>
                            <div class="false-user-poptip">
                                <p>注：最大参与游戏最多选择两项，正常玩家无法在多个游戏中期期不漏的投注订单</p>
                                <p>如果在多个游戏中增加虚拟玩家，请分别建立虚拟用户，并指定不同的游戏</p>
                            </div>
                        </div>
                        <div class="game-list">
                            <div class="game-item" v-for="(item, index) in modifyFalseUser.selectGames" :key="index">
                                <switchs :open="item.Check" @switch="handleSwitchGame(item)"></switchs>
                                <p v-text="gameList.find(ele => ele.type === item.GameType).name"></p>
                                <span>下注行为</span>
                                <select v-model="item.BehaviorID">
                                    <option v-for="(ele, ind) in behaviorList" :key="ind" :value="ele.id">{{ele.name}}</option>
                                </select>
                            </div>
                        </div>
                    </div>
                    <div class="modify-operate">
                        <div class="enter-button" @click="handleModifyFalseUser">确定</div>
                        <div class="cancel-button" @click="handleCloseModal('modifyFalseUser')">取消</div>
                    </div>
                    <div class="modify-footer"></div>
                </div>
            </div>
        </div>
        <Modal v-model="showDelete" :styles="{top: '200px'}" width="300">
            <p slot="header" style="color: #0d1941;font-size: 16px;text-align:center">
                <span>确认删除</span>
            </p>
            <p style="text-align:center">是否确认删除？</p>
            <div slot="footer" class="modal-footer">
                <div class="enter-cancel" @click="showDelete = false">取消</div>
                <div class="enter-ok" @click="handleEnter">确定</div>
            </div>
        </Modal>
        <input v-show="false" ref="uploadImg" type="file"/>
        <aiSetting :aiShow="AIShow" :id="AIID" :userId="AIUSERID" @closeModal="handleCloseAIModal" @editBehavior="handleEditBehavior"></aiSetting>
        <my-upload @crop-success="handleCropSuccess"
            v-model="showUploadHeader"
            :width="100"
            :height="100"
            img-format="png"></my-upload>
        <!-- <loading :loading="showLoading"></loading> -->
    </div>
</template>

<script>
import aiSetting from '../../component/AISetting.vue';
import switchs from '../../component/switch.vue';
import myUpload from 'vue-image-crop-upload';
export default {
    components: {
        aiSetting,
        switchs,
        myUpload
    },
    data() {
        let that = this;
        return {
            page: {
                total: 0,
                pageSize: 10,
                pageNum: 1
            },
            showLoading: false,
            AIID: '',
            AIUSERID: '',
            AIShow: false,
            addFalseUser: {
                show: false,
                nickname: '',
                header: '',
                status: 'prohibit'
            },
            modifyFalseUser: {
                show: false,
                id: '',
                showId: '',
                nickname: '',
                header: '',
                selectGames: [],
                userId: '',
                isLoading: false,
                gameType: '1',
                inputText: ''
            },
            gameList: [],
            falseUserStatusList: [
                {
                    title: '启用',
                    value: 'enable'
                },
                {
                    title: '禁用',
                    value: 'prohibit'
                }
            ],
            showUploadHeader: false,
            falseUserData: [],
            isLoading: false,
            showDelete: false,
            uploadImg: '',
            showUploadImg: '',
            behaviorList: [],
            behaviorId: '',
            gameStatus: []
        }
    },
    mounted() {
        let that = this;

        that.getGameList().then(function() {
            that.getData();
            that.handleGetBehaviorList();
            that.getRoomDataStatus();
        })
    },
    methods: {
        handleSend() {
            let that = this;

            if (!that.modifyFalseUser.inputText) {
                return false;
            }
            that.$axios({
                url: '/api/Setup/ShamUserSendMessage',
                params: {
                    userID: that.modifyFalseUser.userId,
                    gameType: that.modifyFalseUser.gameType,
                    message: that.modifyFalseUser.inputText
                }
            }).then(function(data) {
                if (data.data.Status == 100) {
                    that.modifyFalseUser.inputText = '';
                    that.$Message.success('发送成功');
                } else {
                    that.$Message.success('发送失败');
                }
            }).catch(function() {
                that.$Message.success('发送失败');
            })
        },
        getRoomDataStatus() {
            let that = this;

            that.$axios({
                url: '/api/Room/GetGameIDs'
            }).then(function(data) {
                if (data.data.Status === 100) {
                    const result = data.data.Data;

                    that.gameStatus = result.map(item => {
                        return {
                            id: item.ID,
                            status: item.Status,
                            type: item.Type
                        }
                    })
                }
            })
        },
        handleCropSuccess(imgDataUrl, field) {
            let that = this,
                formData = new FormData(),
                file = that.dataURItoFile(imgDataUrl);

            if (file.size > 5 * 1024 * 1024) {
                that.$Message.error('上传图片不能超过5M');
                return;
            }
            that.showUploadImg = imgDataUrl;
            if (!that.isLoading) {
                that.isLoading = true;

                formData.append('fileinput', file);
                that.$axios({
                    url: '/api/User/UpdateUserImages',
                    method: 'post',
                    params: {
                        userID: that.modifyFalseUser.userId
                    },
                    data: formData
                }).then(function(data) {
                    that.isLoading = false;
                    that.$Message.success(data.data.Message);
                    if (data.data.Status === 100) {
                        that.getData();
                    }
                })
            }
        },
        dataURItoFile(base64Data) {
            var byteString;
            if(base64Data.split(',')[0].indexOf('base64') >= 0)
                byteString = atob(base64Data.split(',')[1]);
            else
                byteString = unescape(base64Data.split(',')[1]);
            var mimeString = base64Data.split(',')[0].split(':')[1].split(';')[0];
            var ia = new Uint8Array(byteString.length);
            for(var i = 0; i < byteString.length; i++) {
                ia[i] = byteString.charCodeAt(i);
            }
            return new File([ia], 'upload.png', {
                type: mimeString
            });
        },
        getGameList() {
            let that = this;

            return new Promise((resolve, reject) => {
                that.$axios({
                    url: '/api/Merchant/GetGameList'
                }).then(function(data) {
                    if (data.data.Status === 100) {
                        const result = data.data.Data;

                        that.gameList = result.map(item => {
                            return {
                                name: item.GameName,
                                type: item.GameType,
                                key: item.NickName,
                                value: 0
                            }
                        }).sort((a, b) => a.type - b.type);
                        resolve();
                    }
                    reject();
                })
            })
        },
        handleImageError(item) {
            item.header = '/UserImages/default.png';
        },
        handleChangeSize(_pageSize) {
            let that = this;

            that.page.pageSize = _pageSize;
            that.page.pageNum = 1;
            that.getData();
        },
        handleChangePage(page) {
            let that = this;

            that.page.pageNum = page;
            that.getData();
        },
        handleEditBehavior(id) {
            let that = this;

            that.AIID = id;
        },
        handleShowEdit(id) {
            let that = this;

            that.AIID = id;
            that.handleShowAI();
        },
        handleShowDelete(id) {
            let that = this;

            that.behaviorId = id;
            that.showDelete = true;
        },
        handleEnter() {
            let that = this;

            if (that.behaviorId) {
                that.handleDeleteBehavior();
            } else {
                that.handleEnterDelete();
            }
        },
        handleDeleteBehavior() {
            let that = this;

            if (!that.isLoading) {
                that.isLoading = true;

                that.$axios({
                    url: '/api/Setup/DeleteBehavior',
                    method: 'post',
                    params: {
                        behaviorID: that.behaviorId
                    }
                }).then(function(data) {
                    if (data.data.Status === 100) {
                        that.$Message.success(data.data.Message);
                    } else {
                        that.$Message.error(data.data.Message);
                    }
                    that.isLoading = false;
                    that.behaviorId = '';
                    that.showDelete = false;
                    that.handleGetBehaviorList();
                })
            }
        },
        handleSwitch(item) {
            let that = this;

            if (that.behaviorList.length < 1) {
                that.$Message.error('没有添加行为，不能启用该虚拟用户');
                return;
            }

            if (!that.isLoading) {
                that.isLoading = true;
                that.showLoading = true;

                that.$axios({
                    url: '/api/Setup/ShamUserOpenOrClose',
                    method: 'post',
                    params: {
                        id: item.id
                    }
                }).then(function(data) {
                    if (data.data.Status === 100) {
                        that.$Message.success(data.data.Message);
                    } else {
                        that.$Message.error(data.data.Message);
                    }
                    that.showLoading = false;
                    that.isLoading = false;
                    that.getData();
                })
            }
        },
        handleSwitchGame(item) {
            let that = this,
                res = that.gameStatus.find(ele => ele.type == item.GameType).status;
            if (res != 1) {
                that.$Message.error('该游戏未开启，不能启用行为!');
                return;
            }
            item.Check = !item.Check;
        },
        handleGetBehaviorList() {
            let that = this;

            if (!that.modifyFalseUser.isLoading) {
                that.modifyFalseUser.isLoading = true;

                that.$axios({
                    url: '/api/Setup/GetBehaviorManage'
                }).then(function(data) {
                    that.modifyFalseUser.isLoading = false;

                    if (data.data.Status === 100) {
                        that.behaviorList = data.data.Data.map(item => {
                            return {
                                name: item.BehaviorName,
                                num: item.Count,
                                id: item.BehaviorID
                            }
                        })
                    }
                })
            }
        },
        handleShowAI() {
            let that = this;

            that.AIShow = true;
        },
        handleCloseAIModal() {
            let that = this;

            that.AIID = '';
            that.handleGetBehaviorList();
            that.AIShow = false;
        },
        handleShowAddUser() {
            let that = this;
            that.addFalseUser.show = true;
        },
        handleOperateUser(bool) {
            let that = this;

            if (!that.isLoading) {
                that.isLoading = true;

                that.$axios({
                    url: '/api/Setup/ShamUserAllOpenOrClost',
                    method: 'post',
                    params: {
                        derail: bool
                    }
                }).then(function(data) {
                    that.isLoading = false;

                    if (data.data.Status === 100) {
                        that.$Message.success(data.data.Message);
                    } else {
                        that.$Message.error(data.data.Message);
                    }
                    that.getData();
                })
            }
        },
        handleShowModifyUser(item) {
            let that = this;

            that.modifyFalseUser.userId = item.userId;
            that.modifyFalseUser.showId = item.showId;
            that.modifyFalseUser.nickname = item.nickname;
            that.modifyFalseUser.loginName = item.loginName;
            that.modifyFalseUser.header = item.header;
            that.modifyFalseUser.selectGames = item.selectGames;
            that.modifyFalseUser.show = true;
        },
        handleDeleteUser(item) {
            let that = this;

            that.modifyFalseUser.id = item.userId;
            that.showDelete = true;
        },
        handleUploadImg() {
            let that = this;
            that.$refs['uploadImg'].onchange = function(e) {
                that.uploadImg = e.target.files[0];

                let URL = window.URL || window.webkitURL;
                that.showUploadImg = URL.createObjectURL(that.uploadImg);
            }
            that.$refs['uploadImg'].click();
        },
        handleAddFalseUser() {
            let that = this;

            if (!that.addFalseUser.nickname) {
                that.$Message.error('请输入用户昵称');
                return;
            }

            if (!that.isLoading) {
                that.isLoading = true;

                that.$axios({
                    url: '/api/Setup/AddShamUser',
                    method: 'post',
                    params: {
                        userName: that.addFalseUser.nickname,
                        derail: false
                    }
                }).then(data => {
                    if (data.data.Status === 100) {
                        that.$Message.success(data.data.Message);
                    } else {
                        that.$Message.error(data.data.Message);
                    }
                    that.isLoading = false;
                    that.addFalseUser.show = false;
                    that.getData();
                })
            }
        },
        handleCloseModal(key) {
            let that = this;
            that[key].show = false;
        },
        handleModifyFalseUser() {
            let that = this,
                vParam = {},
                vData = {};

            if (!that.modifyFalseUser.nickname) {
                that.$Message.error('请填写用户昵称');
                return;
            } else {
                vParam['nickName'] = that.modifyFalseUser.nickname;
            }

            let res = that.modifyFalseUser.selectGames.find(ele => ele.Check && !ele.BehaviorID);

            if (res) {
                that.$Message.error('启用游戏后，必须选择下注行为');
                return;
            }

            res = that.modifyFalseUser.selectGames.filter(ele => ele.Check);

            if (res && res.length > 2) {
                that.$Message.error('最大只能选择两个不同的彩种');
                return;
            }

            vParam['userID'] = that.modifyFalseUser.userId;

            vData['gameCheckInfo'] = [];

            that.modifyFalseUser.selectGames.forEach(item => {
                vData['gameCheckInfo'].push({
                    "GameType": item.GameType,
                    "Check": item.Check,
                    "BehaviorID": item.BehaviorID
                })
            })

            if (!that.isLoading) {
                that.isLoading = true;

                that.$axios({
                    url: '/api/Setup/PutShamUserInfo',
                    method: 'post',
                    params: vParam,
                    data: vData
                }).then(function(data) {
                    if (data.data.Status === 100) {
                        that.$Message.success(data.data.Message);
                    } else {
                        that.$Message.error(data.data.Message);
                    }

                    that.isLoading = false;
                    that.modifyFalseUser.show = false;
                    that.getData();
                })
            }
        },
        getData() {
            let that = this,
                param = {};

            param['start'] = that.page.pageNum;
            param['pageSize'] = that.page.pageSize;

            if (!that.isLoading) {
                that.isLoading = true;
                that.showLoading = true;

                that.$axios({
                    url: '/api/Setup/GetShamUserListInfo',
                    params: param
                }).then(function(data) {
                    if (data.data.Status === 100) {
                        const result = data.data.Data;

                        that.page.total = data.data.Total;
                        that.falseUserData = result.map(item => {
                            return {
                                id: item.ID,
                                showId: item.OnlyCode,
                                balance: item.UserMoney,
                                nickname: item.NickName,
                                loginName: item.LoginName,
                                header: item.Avatar ? (item.Avatar.indexOf('://') === -1 ? ('/' + item.Avatar) : item.Avatar) : '',
                                selectGames: item.GameBetInfo,
                                userId: item.UserID,
                                openGame: item.OpenGame,
                                open: item.Check
                            }
                        })
                    }

                    that.isLoading = false;
                    that.showLoading = false;
                })
            }
        },
        handleEnterDelete() {
            let that = this;

            if (!that.isLoading) {
                that.isLoading = true;

                that.$axios({
                    url: '/api/User/DeleleUser',
                    method: 'post',
                    params: {
                        userID: that.modifyFalseUser.id
                    }
                }).then(function(data) {
                    if (data.data.Status === 100) {
                        that.$Message.success(data.data.Message);
                    } else {
                        that.$Message.error(data.data.Message);
                    }
                    that.isLoading = false;
                    that.showDelete = false;
                    if (that.page.total % that.page.pageSize === 1) {
                        that.page.pageNum = parseInt(that.page.total / that.page.pageSize);
                    }
                    that.getData();
                })
            }
        }
    },
}
</script>

<style lang="scss" scoped>
.false-user {
    display: flex;

    position: relative;

    margin-top: 10px;

    .false-user-mask {
        display: flex;
        align-items: center;
        justify-content: center;
        position: fixed;
        top: 0;
        bottom: 0;
        left: 0;
        right: 0;

        width: 100%;
        height: 100%;

        background-color: rgba(55,55,55,.6);

        z-index: 1000;

        .add-false-user, .modify-false-user {
            // position: fixed;
            // top: 0;
            // right: 0;
            // bottom: 0;
            // left: 0;

            margin: auto;

            width: 400px;

            border: solid 3px rgb(204, 204, 204);
            border-radius: 4px;

            background-color: #fff;

            z-index: 1001;
            overflow: hidden;

            .add-header, .modify-header {
                display: flex;
